body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.background{
  position: absolute;
  left: 0;
  top: 0;
  z-index: -10;
  opacity: 0.7;
  display: block;
  -webkit-filter: blur(1px);
  -moz-filter: blur(1px);
  -o-filter: blur(1px);
  -ms-filter: blur(1px);
  filter: blur(1px);
  background-position: 0% 0%;
}
/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 5px grey;
          box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: gray;
  border-radius: 10px;
}

.center{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:row ;
      flex-direction:row ;
  -ms-flex-pack:center;
      justify-content:center;
  -ms-flex-align:center;
      align-items:center;
}

.clickable {
  cursor: pointer;
}

/**
 * Import pages
 */
@import './css/_classSettings.css';



